//Vue
import Vue from 'vue'
//Vuex
import Vuex from 'vuex'
//Vuex
Vue.use(Vuex)
/**
 * vuex
 * @type {{}}
 */


//actions
const actions = {}
//mutationsstate
const mutations = {
    // vuextoken
    STORAGE_TOKEN(state, token){
        localStorage.setItem("token", token)
        state.token = token
    },
    // vuexusername
    STORAGE_USER_NAME(state, username){
        localStorage.setItem("username", username)
        state.user = username
    },

    // usernametoken
    CLEAN_LOGIN_DATA(state){
        localStorage.clear()
        state.token = null;
        state.username = null;
    }
}
//state
const state = {
    username: null,
    token: null
}

//store
let store = new Vuex.Store({
    actions,
    mutations,
    state
})

export default store
